.App {
  text-align: center;
  white-space: nowrap;
}

.App-logo {
  -webkit-animation: fade infinite 1s linear;
          animation: fade infinite 1s linear;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes fade {
  0%,100% { opacity: 0.5 }
  50% { opacity: 1 }
}

@keyframes fade {
  0%,100% { opacity: 0.5 }
  50% { opacity: 1 }
}



